@import '../../shared-styles/_colors.scss';

body {
  padding-top: 100px;
}

h1 {
  text-align: center;
  font-size: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  color: $accent;
}

.img {
  width: 300px;
  height: 200px;
  margin: 0 auto;
}

a {
  color: seagreen;
  font-size: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  margin-bottom: 15px;
}

a:hover {
  color: orange;
}

@media screen and (min-width: 650px) {
  h1 {
    text-align: center;
    font-size: 40px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    color: $accent;
  }
  .img {
    width: 600px;
    height: 400px;
  }
  a {
    color: seagreen;
    font-size: 40px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
  }

  .link {
    text-decoration: none;
    color: seagreen;
  }
}

@media screen and (min-width: 1100px) {
  h1 {
    text-align: center;
    font-size: 30px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    color: $accent;
  }
  .img {
    width: 600px;
    height: 400px;
  }

  a {
    color: seagreen;
    font-size: 30px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
  }

  .link {
    color: seagreen;
  }
}
